<template>
    <div class="globlgl  globlgl--custom">
        <div class="title">
            <div class="title1">企业账号</div>
            <div class="item">公司名称： {{showCheck.companyName}}</div>
            <div class="item">账号： {{showCheck.account}}</div>
        </div>
        <div class="title">
            <div class="title1">开通情况</div>
            <div class="item">总机： {{showCheck.switchboard}}</div>
            <div class="item">IVR： {{showCheck.IVRStatus}}</div>
            <div class="item">分机个数： {{showCheck.extensionCount}}</div>
            <div class="item">分机录音个数： {{showCheck.extRecordCount}}</div>
            <div class="item">分机组个数： {{showCheck.extGroupCount}}</div>
            <div class="item">外显号码： 
              <div class='numberItem'>
                <div class="item1" v-for="item in showCheck.numbers">
                  {{item}}
                </div>
              </div>
              
            </div>
        </div>
    </div>
</template>

<script>

  import {
      getCompanyInfo,
      getCompanyLimitByVlanId,
      getExtraNumber,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
 
  export default {
    data() {

        return {
            showCheck:{
                account:'',
                companyName:'',
                switchboard:'',
                IVRStatus:'',
                extensionCount:'',
                extRecordCount:'',
                extGroupCount:'',
                numbers:[],
            }
        }
    },
    created() {
      this.getCompanyInfo();
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      async getCompanyInfo(){
        try {
            const res = await getCompanyInfo({
                vlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
            });
            if (res.resultCode == '0') {
                this.showCheck.companyName = res.result.companyname;
                this.showCheck.account = res.result.username;
            } else {
                throw new Error(res.message)
            }
            const resLimit = await getCompanyLimitByVlanId({
              vlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
            });
            if (resLimit.resultCode == '0') {
                this.showCheck.extensionCount = resLimit.result.extNumber;
                this.showCheck.switchboard = resLimit.result.rootNumber;
                this.showCheck.extRecordCount = resLimit.result.recordNumber;
                this.showCheck.extGroupCount = resLimit.result.extgNumber;
                if(resLimit.result.ivrState == 0){
                  this.showCheck.IVRStatus = '开通';
                } else {
                  this.showCheck.IVRStatus = '停用';
                }
            } else {
                throw new Error(resLimit.message)
            }
            const resNumble = await getExtraNumber({
              vlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
            });
            if (resNumble.resultCode == '0') {
                resNumble.result.forEach(item => {
                  this.showCheck.numbers.push(item.extraNumber);
                });
            } else {
                throw new Error(resNumble.message)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      
    }
  }

</script>
<style scope lang="less">
  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    } 
    .title{
        font-size:100%;
    }
    .item{
        margin: 30px;
        margin-left: 40px;
    }
    .item1{
      margin: 10px;
      margin-left: 67px;
    }
    .numberItem{
      margin-top: -23px;
    }
    .title1{
        font-weight:bold;
        margin: 20px;
    }

  }

 
</style>





