<template>
    <div class="tongji-wrapper">
        <div class="menu tac">
            <el-row>
                <el-col>
                    <el-menu :default-active="defaultMenu" @select="openMenu">
                        <el-submenu index="accountInfo" v-show="accountInfomenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4">
                            </i><span>企业账户</span></template>                            
                            <el-menu-item index="companyAccountInfo" v-show="companyAccountInfomenu">企业账户</el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-col>
            </el-row>
        </div>
        <div class="main">
            <companyAccountInfo v-if="selectMenuName=='companyAccountInfo'"></companyAccountInfo>
        </div>
    </div>
</template>

<script>
import companyAccountInfo from "./companyAccountInfo.vue"
export default {
    components: {
      companyAccountInfo: companyAccountInfo,
    },
    data() {
        return {
            defaultMenu:'',
            selectMenuName: '',
            
            companyAccountInfomenu:false,
            accountInfomenu:false,
        }
    },
    created(){
        let userInfo = this.$store.baseStore.getters.getUserInfo;
        let secondmenu = userInfo.menu.secondmenuset;
        if(secondmenu.indexOf("企业账户") != -1){
            this.$data.accountInfomenu = true;
        }

        let thirdmenu = userInfo.menu.thirdmenuset;
        let defaultmenu = "";
        if(thirdmenu.indexOf("企业账户") != -1){
            this.$data.companyAccountInfomenu = true
            defaultmenu = "companyAccountInfo";
        }
        
        this.$data.defaultMenu = defaultmenu;
        this.$data.selectMenuName =  defaultmenu;

        //记住菜单功能，即刷新后还能回到这，firstDegreeMenu左侧一级大菜单，secondDegreMenu 二级菜单，thirdmenu表示三级菜单
        var currentSelectMenu = this.$store.baseStore.getters.getCurrentSelectMenu;
        if(currentSelectMenu != undefined && currentSelectMenu.secondDegreMenu != undefined
            && currentSelectMenu.secondDegreMenu != '' && currentSelectMenu.firstDegreeMenu == 'enterpriseAccount'){
            this.$data.selectMenuName = currentSelectMenu.secondDegreMenu;
            this.$data.defaultMenu = currentSelectMenu.secondDegreMenu;
        }else{
            var menu = new Object();
            menu['secondDegreMenu'] = defaultmenu;
            menu['firstDegreeMenu'] = 'enterpriseAccount';
            this.$store.baseStore.commit('setCurrentSelectMenu',menu);
        }

    },
    methods: {
        /**
         * 打开菜单
         */
        openMenu: function(index, indexPath) {
            this.$data.selectMenuName = index;
            var menu = new Object();
            menu['secondDegreMenu'] = index;
            menu['firstDegreeMenu'] = 'extAccount';
            this.$store.baseStore.commit('setCurrentSelectMenu',menu);
        },

        // resize 重置菜单和内容高度
        handleWindowResize() {
          const selector = (selectorName) => {
            return document.querySelector(selectorName);
          };
          const height = window.innerHeight - 50;
          
          selector('.main').style.height = height + 'px';
          selector('.menu.tac').style.height = height + 'px';
          selector('.tongji-wrapper').style.height = height + 'px';
        }, 
    },
    mounted() {
        this.handleWindowResize();
        window.addEventListener('resize', this.handleWindowResize);
    },
    
    beforeDestroy () {
      window.removeEventListener('resize', this.handleWindowResize);
    },
}
</script>

